<template>
  <el-upload
    action=""
    :show-file-list="showFileList"
    :accept="actualAccept"
    :data="data"
    :list-type="listType"
    :file-list="fileList"
    :before-upload="handleBeforeUpload"
    :http-request="customUpload"
    :disabled="disabled"
    :multiple="multiple"
    :limit="limit"
    :on-exceed="handleExceed"
  >
    <div :id="triggerId">
      <slot>
        <el-button size="small" type="primary" :disabled="disabled">
          
        </el-button>
      </slot>
    </div>
  </el-upload>
</template>

<script>
// TODO  https://github.com/dai-siki/vue-image-crop-upload/blob/master/upload-2.vue

import { uploaderDefault } from "../index.js";
import { fixImgFile } from "ios-photo-repair";

/**
 * 
 * @param filename[String] 
 * @return 
 */
export const getSuffix = (filename) => {
  let pos = filename.lastIndexOf(".");
  let suffix = "";
  if (pos != -1) {
    suffix = filename.substring(pos + 1);
  }
  return suffix.toLowerCase();
};

/**
 * 
 * @param type[String] FileTypeMap 
 * @return 
 * */
const FileTypeMap = uploaderDefault.FileTypeMap || {};

export const getExtByType = (type) => {
  if (type && Array.isArray(FileTypeMap[type])) {
    let classList = [];
    let extList = [];
    FileTypeMap[type].forEach((e) => {
      if (e.indexOf("t-") === 0) {
        classList.push(e);
      } else {
        extList.push(e);
      }
    });
    if (classList.length) {
      classList.forEach((classType) => {
        extList = extList.concat(getExtByType(classType));
      });
    }
    return extList;
  } else if (type && type.split) {
    return [type.toLowerCase()];
  }
};

export default {
  name: "Uploader",
  props: {
    multiple: {
      type: Boolean,
      required: false,
      default: uploaderDefault.multiple,
    },
    data: {
      type: Object,
      required: false,
      default: uploaderDefault.data,
    },
    name: {
      type: String,
      required: false,
      default: uploaderDefault.name,
    },
    showFileList: {
      type: Boolean,
      required: false,
      default: uploaderDefault.showFileList,
    },
    accept: {
      type: String,
      required: false,
      default: uploaderDefault.accept,
    },
    listType: {
      type: String,
      required: false,
      default: uploaderDefault.listType,
    },
    fileList: {
      type: Array,
      required: false,
      default: uploaderDefault.fileList,
    },
    disabled: {
      type: Boolean,
      required: false,
      default: uploaderDefault.disabled,
    },
    limit: {
      type: Number,
      required: false,
      default: uploaderDefault.limit,
    },
    beforeUpload: {
      type: Function,
      required: false,
      default: uploaderDefault.beforeUpload,
    },
    triggerId: {
      type: String,
      required: false,
      default: "upload_image_trigger" + parseInt(Math.random() * 1e8),
    },
    imgCompress: {
      type: Boolean,
      required: false,
      default: uploaderDefault.imgCompress,
    },
    imgCompressOption: {
      type: Object,
      required: false,
      default: uploaderDefault.imgCompressOption,
    },
    uploadFunc: {
      type: Function,
      required: false,
      default: uploaderDefault.uploadFunc,
    },
    uploadBase64Func: {
      type: Function,
      required: false,
      default: uploaderDefault.uploadBase64Func,
    },
    limitSize: {
      type: Number,
      required: false,
      default: uploaderDefault.limitSize,
    },
  },
  computed: {
    actualAccept() {
      if (this.accept.indexOf("t-") !== -1) {
        const typeArray = this.accept.split(",");
        let result = [];
        typeArray.forEach((type) => {
          result = result.concat(getExtByType(type));
        });
        return result.join(",");
      } else {
        return this.accept;
      }
    },
  },
  methods: {
    handleExceed() {
      this.$emit("exceed");
    },
    handleSuccess: function (res) {
      this.$emit("success", res.data);
    },
    handleError: function (err) {
      this.$emit("error", err);
    },
    handleBeforeUpload: function (file) {
      // 
      const fileExt = getSuffix(file.name);
      const extWhite = this.actualAccept.replace(/\./g, "").split(",");
      if (extWhite.findIndex((ext) => ext === "*" || ext === fileExt) === -1) {
        this.$message.warning("");
        return false;
      }
      // 
      if (file.size > this.limitSize) {
        this.$message.warning("");
        return false;
      }
      // 500
      if (file.name.length > 500) {
        this.$message.warning("500");
        return false;
      }
      // 
      return this.beforeUpload(file);
    },
    customUpload: function (params) {
      if (this.imgCompress && params.file.type.indexOf("image/") === 0) {
        // 
        fixImgFile(params.file, this.imgCompressOption).then((base64) => {
          const name = params.file.name.replace(/\.[^.]+\w+$/, ".png");
          if (base64 && name) {
            this.uploadBase64Func({
              base64,
              name,
            })
              .then((res) => {
                this.handleSuccess(res.data);
              })
              .catch((err) => {
                this.handleError(err);
              });
          } else {
            console.warn("error", base64, name);
          }
        });
      } else {
        // 
        let formData = new FormData();
        formData.append(this.name, params.file);
        // 
        Object.keys(this.data).forEach((key) => {
          formData.append(key, this.data[key]);
        });
        this.uploadFunc(formData)
          .then((res) => {
            this.handleSuccess(res.data);
          })
          .catch((err) => {
            this.handleError(err);
          });
      }
    },
  },
};
</script>

<style scoped>
</style>

